import { NavLink } from "react-router-dom";

function NavButton(props) {
	return (
		<NavLink
			to={props.to}
			className="px-3 py-2 mx-2 rounded-lg font-semibold text-center transition-colors bg-transparent hover:bg-gray-500"
		>
			{props.label}
		</NavLink>
	);
}

export default function Navbar(props) {
	return (
		<div className="sticky top-0 flex justify-between items-center bg-black text-white p-3 z-10">
			<div className="flex content-center">
				<p className="font-bold text-xl text-center px-3 py-1">BANHI</p>
			</div>
			<div className="flex items-center">
				<NavButton to="/" label="HOME" />
				<NavButton to="/issues" label="ISSUES" />
				<NavButton to="/" label="ABOUT US" />
				<NavButton to="/" label="CONTACT" />
			</div>
		</div>
	);
}
