import WebViewer from "@pdftron/pdfjs-express-viewer";
import { useEffect, useRef } from "react";

export default function Issues(props) {
	const viewer = useRef(null);

	useEffect(() => {
		WebViewer(
			{
				licenseKey: "Wn47D7inGKRiaBQlun4f",
				path: "/webviewer/lib",
				initialDoc: "/2022june15.pdf",
				disabledElements: ["downloadButton", "printButton"],
			},
			viewer.current
		).then((instance) => {
			const { Core } = instance;
			instance.setTheme("dark");
			instance.setFitMode(instance.FitMode.FitPage);

			Core.documentViewer.addEventListener("documentLoaded", () => {
				console.log("document loaded");
			});

			// adding an event listener for when the page number has changed
			Core.documentViewer.addEventListener("pageNumberUpdated", (pageNumber) => {
				console.log(`Page number is: ${pageNumber}`);
			});
		});
	}, []);

	return (
		<div className="grow flex w-full">
			<div className="grow" ref={viewer}></div>
		</div>
	);
}
