import BanhiLogo from "../assets/banhi.webp";
import EditerProfile from "../assets/edpf.jpg";

function Section(props) {
	return (
		<div id={props.id} className="flex flex-col gap-4 p-8 section-bg items-center">
			{props.children}
		</div>
	);
}

function SectionTitle(props) {
	return (
		<p className="basis-full text-3xl font-normal text-white" style={{ fontFamily: "Great Vibes" }}>
			{props.children}
		</p>
	);
}

function Welcome(props) {
	return (
		<Section id="welcome">
			<SectionTitle>Welcome to</SectionTitle>
			<div className="grow flex flex-col gap-8 items-center w-full">
				<img src={BanhiLogo} alt="cover" className="max-h-32 rounded-lg my-5" />
				<p className="text-lg text-center font-medium text-white max-w-prose">
					We are an organization looking forward to inspire content writers and artists to find new
					horizons etc etc. Voluptate enim anim proident sunt nostrud proident. Duis officia qui
					eiusmod ipsum non sit incididunt. Lorem nostrud eiusmod amet pariatur nisi duis incididunt
					est. Labore aliqua mollit est excepteur et adipisicing minim.
				</p>
			</div>
		</Section>
	);
}

function LatestEdition(props) {
	return (
		<Section id="latest-edition-bg">
			<SectionTitle>Our latest edition</SectionTitle>
			<div className="grow flex flex-col gap-8 items-center w-full">
				<img src="./banhi.png" alt="cover" className="w-56 rounded-lg" />
				<div id="home-desc" className="flex flex-col items-center text-center gap-4">
					<p className="text-4xl font-extrabold text-white max-w-prose">
						Some title related to Instagram
					</p>
					<hr
						className="w-1/3 h-0.5 bg-white"
						style={{
							boxShadow: "0 0 3px white",
						}}
					/>
					<p className="text-lg font-medium text-white max-w-prose">
						Some description about why you chose Instagram as the topic of this month's edition, etc
						etc.
					</p>
				</div>
			</div>
		</Section>
	);
}

function EditorNotes(props) {
	return (
		<Section id="editor-bg">
			<SectionTitle>From the Editor</SectionTitle>
			<div className="grow flex flex-col gap-8 items-center w-full">
				<img
					src={EditerProfile}
					alt="cover"
					className="w-56 h-56 rounded-full object-cover object-center"
				/>
				<div id="home-desc" className="flex flex-col items-center text-center gap-4">
					<p className="text-4xl font-extrabold text-white max-w-prose">
						Your Name<span className="text-base font-light text-white max-w-prose">, Student</span>
					</p>
					<hr
						className="w-1/3 h-0.5 bg-white"
						style={{
							boxShadow: "0 0 3px white",
						}}
					/>
					<p className="text-lg font-medium text-white max-w-prose">
						Labore et nulla aliquip adipisicing proident in culpa proident. Nisi exercitation magna
						ipsum non enim dolor est sunt labore. Aute duis enim duis non excepteur ipsum laborum
						nulla ipsum fugiat.
					</p>
				</div>
			</div>
		</Section>
	);
}

export default function Home(props) {
	return (
		<>
			<Welcome />
			<LatestEdition />
			<EditorNotes />
		</>
	);
}
